@import url(https://fonts.googleapis.com/css?family=Holtwood+One+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html,
body,
#root {
  height: 100%;
  overflow: hidden;
  font-size: 16px;
}

.Hangman {
  border: 1px solid #ccc;
  padding: 16px;
  margin: 0;
}

.bamtitle {
    height: 60px;
    margin: 32px auto 16px;
    font-size: 60px;
    font-family: "Helvetica", sans-serif;
    font-weight: bold;
}

.bamtitle span {
    display: inline-block;
}

.bamtitle .animated {
    -webkit-animation-name: bam;
            animation-name: bam;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes bam {
    from {-webkit-transform: scale(3.0);transform: scale(3.0);}
    to {-webkit-transform: scale(1.0);transform: scale(1.0);}
}

@keyframes bam {
    from {-webkit-transform: scale(3.0);transform: scale(3.0);}
    to {-webkit-transform: scale(1.0);transform: scale(1.0);}
}

.play-container .hang-container {
    overflow: hidden;
    position: relative;
    width: 500px;
    height: 503px;
}

.play-container .title-container {
    width: 500px;
    height: 108px;
    overflow: hidden;
}

.play-container > p {
    font-family: Helvetica, sans-serif;
    font-size: 27px;
    text-align: center;
}
.revealed {
    font-family: 'Helvetica', sans-serif;
    font-size: 40px;
    margin-top: 30px;
    margin-bottom: 0;
    max-width: 500px;
    text-align: center;
}

.letter {
    display: inline-block;
    margin-bottom: 16px;
}

.letter:not(:first-child) {
    margin-left: 15px;
}

.letter > span {
    height: 40px;
    width: 40px;
    text-align: center;
    display: block;
    font-stretch: expanded;
    font-weight: bold;
}

.letter > div {
    width: 40px;
    height: 10px;
    background-color: black;
    display: block;
}
.gameover {
    width: 500px;
    height: 500px;
    display: block;
    margin-top: 100px;
}

.gameover .message > p:first-child {
    padding-top: 12px;
}

.gameover .message > p {
    text-align: center;
    font-size: 36px;
    line-height: 24px;
    font-family: "Helvetica", sans-serif;
}

.gameover .scoreboard {
    margin-bottom: 15px;
}

.gameover .reset-stats-button {
    margin-bottom: 50px;
}

.title-window {
    width: 500px;
}

.title-window > p:first-of-type {
    text-align: center;
    font-size: 36px;
    margin: 24px 0;

    font-family: "Helvetica", sans-serif;
}

.title-window .scoreboard {
    margin-top: 30px;
    margin-bottom: 15px;
}

.title-window .reset-stats-button {
    margin-bottom: 50px;
}

.hang-title {
    width: 500px;
    height: 450px;
    margin-top: 25px;
    font-family: 'Holtwood One SC', 'Helvetica', sans-serif;
    font-size: 80px;
    position: relative;
}

.hang-title > span, .hang-title > div {
    -webkit-animation-duration: 250ms;
            animation-duration: 250ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
}

.hang-title > span {
    position: absolute;
    line-height: 80px;
    -webkit-animation-name: appearScale;
            animation-name: appearScale;
}

.hang-title > span:nth-child(1) {
    margin-left: -44px;
    left: 250px;
}

.hang-title > span:nth-child(2) {
    margin-left: -35px;
    left: 130px;
    top: 120px;
    -webkit-animation-delay: 400ms;
            animation-delay: 400ms;
}

.hang-title > span:nth-child(3) {
    margin-left: -39px;
    left: 250px;
    top: 120px;
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;
}

.hang-title > span:nth-child(4) {
    margin-left: -32px;
    left: 370px;
    top: 120px;
    -webkit-animation-delay: 600ms;
            animation-delay: 600ms;
}

.hang-title > span:nth-child(5) {
    margin-left: -46px;
    left: 130px;
    top: 360px;
    -webkit-animation-delay: 1000ms;
            animation-delay: 1000ms;
}

.hang-title > span:nth-child(6) {
    margin-left: -35px;
    left: 250px;
    top: 240px;
    -webkit-animation-delay: 800ms;
            animation-delay: 800ms;
}

.hang-title > span:nth-child(7) {
    margin-left: -39px;
    left: 370px;
    top: 360px;
    -webkit-animation-delay: 1200ms;
            animation-delay: 1200ms;
}

.hang-title > div {
    background-color: black;
    position: absolute;
    display: block;
    box-sizing: border-box;
    width: 15px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation-name: appear;
            animation-name: appear;
}

.hang-title > div:nth-child(8) {
    height: 35px;
    left: 250px;
    margin-left: -7.5px;
    top: 82.5px;
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms;
}

.hang-title > div:nth-child(9) {
    height: 30px;
    left: 195px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    margin-left: -15px;
    top: 150px;
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
}

.hang-title > div:nth-child(10) {
    height: 30px;
    left: 318px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    margin-left: -15px;
    top: 150px;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
}

.hang-title > div:nth-child(11) {
    height: 35px;
    left: 250px;
    margin-left: -7.5px;
    top: 202.5px;
    -webkit-animation-delay: 700ms;
            animation-delay: 700ms;
}

.hang-title > div:nth-child(12) {
    height: 50px;
    left: 195px;
    margin-left: -7.5px;
    top: 315px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation-delay: 900ms;
            animation-delay: 900ms;
}

.hang-title > div:nth-child(13) {
    height: 50px;
    left: 305px;
    margin-left: -7.5px;
    top: 315px;
    -webkit-transform: scale(1.0) rotate(-45deg);
            transform: scale(1.0) rotate(-45deg);
    -webkit-animation-delay: 1100ms;
            animation-delay: 1100ms;
}

@-webkit-keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes appearScale {
    from {
        transform: scale(2.0);
        -webkit-transform: scale(2.0);
        -moz-transform: scale(2.0);
        -ms-transform: scale(2.0);
        -o-transform: scale(2.0);
        opacity: 0;
    }
    to {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        opacity: 1;
    }
}

@keyframes appearScale {
    from {
        transform: scale(2.0);
        -webkit-transform: scale(2.0);
        -moz-transform: scale(2.0);
        -ms-transform: scale(2.0);
        -o-transform: scale(2.0);
        opacity: 0;
    }
    to {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        opacity: 1;
    }
}
.wasted {
    width: 503px;
    height: 503px;
    left: -1.5px;
    top: 0;

    position: absolute;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-name: wasted;
            animation-name: wasted;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes wasted {
    from {
        background-color: transparent;
        -webkit-transform: scale(1.5) rotate(-30deg);
                transform: scale(1.5) rotate(-30deg);
        opacity: 0;
    }
    to {
        background-color: rgba(128, 128, 128, 0.5);
        -webkit-transform: scale(1.0) rotate(0deg);
                transform: scale(1.0) rotate(0deg);
        opacity: 1;
    }
}

@keyframes wasted {
    from {
        background-color: transparent;
        -webkit-transform: scale(1.5) rotate(-30deg);
                transform: scale(1.5) rotate(-30deg);
        opacity: 0;
    }
    to {
        background-color: rgba(128, 128, 128, 0.5);
        -webkit-transform: scale(1.0) rotate(0deg);
                transform: scale(1.0) rotate(0deg);
        opacity: 1;
    }
}

button.new-game-button {
    width: 300px;
    height: 75px;
    margin-left: 100px;
    background-color: #FFFFFF;
    border: 3px solid black;
    font: 30px 'Helvetica';
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    outline: none;
}

.scoreboard {
    width: 300px;
    height: 150px;
    display: block;
    margin: 50px auto;
}

.scoreboard > div {
    display: inline-block;
}

.scoreboard .divider {
    width: 12px;
    height: 150px;
    background-color: black;
}

.scoreboard .score {
    width: 144px;
    vertical-align: top;
}

.scoreboard .score > span {
    text-align: center;
    font-family: "Helvetica", sans-serif;
    font-weight: bold;
    display: block;
    width: 144px;
}

.scoreboard .score > span:nth-child(1) {
    font-size: 36px;
    margin-top: 20px;
}

.scoreboard .score > span:nth-child(2) {
    margin-top: 24px;
    font-size: 30px;
}

button.reset-stats-button {
    width: 200px;
    height: 50px;
    margin-left: calc(-100px + 50%);
    background-color: #FFFFFF;
    border: 3px solid black;
    font: 24px 'Helvetica';
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    outline: none;
}
.used-letters {
    margin-top: 20px;
    font-size: 40px;
    font-family: "Courier New", sans-serif;
    font-weight: bolder;
    -webkit-font-feature-settings: "c2sc";
            font-feature-settings: "c2sc";
    font-variant: small-caps;
    text-align: center;
    width: 500px;
}

.used-letters > span {
    margin: 0 10px 15px;
    display: inline-block;
}

.used-letters > span.crossed {
    color: #aeaeae;
    text-decoration: line-through;
}
.health-bar {
    position: relative;
    width: 406px;
    height: 26px;
    margin-left: 50px;
    display: block;
    box-sizing: content-box;
    margin-bottom: 10px;
    -webkit-clip-path: polygon(0 0, 380px 0, 406px 26px, 0 26px);
            clip-path: polygon(0 0, 380px 0, 406px 26px, 0 26px);
    background-color: black;
}

.health-bar > div {
    -webkit-clip-path: polygon(0 0, 375.5px 0, 395.5px 20px, 0 20px);
            clip-path: polygon(0 0, 375.5px 0, 395.5px 20px, 0 20px);
    position: absolute;
    top: 3px;
    left: 3px;
    width: 400px;
    height: 20px;
}

.health-bar .background {
    background-color: white;
}

.health-bar .health-juice {
    background-color: #d50000;
}

.health-bar .health-juice.pulsing {
    -webkit-animation-name: pulseRed;
            animation-name: pulseRed;
    -webkit-animation-duration: 1000ms;
            animation-duration: 1000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes pulseRed {
    0% {
        background-color: #d50000;
    }

    50% {
        background-color: #de9b9b;
    }

    100% {
        background-color: #d50000;
    }
}

@keyframes pulseRed {
    0% {
        background-color: #d50000;
    }

    50% {
        background-color: #de9b9b;
    }

    100% {
        background-color: #d50000;
    }
}
.App {
  height: 100%;
  overflow: hidden;
  padding: 16px;
}

.App .container {
  width: 500px;
  margin: 0 auto;
}

body {
  background-color: white;
}
